      font-size: rem-calc(24px);
      margin-top: 5px;
    }

    ul {
      list-style: none;
      padding: 0;
      margin: 40px 0 0 0;

      li {
        color: $color-white;
        font-size: rem-calc(18px);
        display: inline-block;
        width: 45%;
        margin-bottom: 30px;
        position: relative;
        padding-left: 42px;

