/******** old easter campaign styles added to support thank you page ********/

.campaign-header {
  $banner-padding: 65px;

  background-color: $secondary-color;
  background-image: url($images_path +'/easter_campaign/watermark.png');
  background-position: center top;
  background-repeat: no-repeat;
  min-height: 200px;
  padding: $banner-padding 0;
  text-align: center;

  @media #{$small-only} {
    background-size: auto 160%;
  }
}

.campaign-body {
  background-color: $colour-black-haze;
  min-height: 500px;
  padding: 10px 0;

  @media #{$medium-up} {
    padding: 70px 0 54px 0;
  }

  .page-title {
    line-height: 1.1;
  }

  p {
    font-size: em-calc(16px);
    line-height: 1.3;

    .button {
      margin-bottom: 19px;
    }
  }

  ul {
    font-size: em-calc(16px);
  }
}

.campaign-form {
  padding: 25px 0;

  @media #{$medium-up} {
    padding: 70px 0;
  }
}

p.terms {
  color: $color-white;
  display: inline-block;
  font-size: rem-calc(12px);
  line-height: 1.3;
  vertical-align: top;
  margin-top: 20px;

  span.not-included {
    display: block;
    font-family: BlockBQRegular, sans-serif;
    font-weight: 900;
    font-size: rem-calc(18px);
  }

  a {
    color: $white;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }
}

.campaign-footer {
  background-color: #F78F1E;
  color: $white;
  padding: 36px 0;

  a {
    color: $ghost;
    text-decoration: underline;
  }

  img {
    margin: 5px 0 15px 0;

    @media #{$medium-up} {
      margin: 65px 0;
    }
  }

  hr {
    margin-bottom: 50px;
  }

  small {
    font-size: em-calc(12);
    line-height: 1;
  }
}

.ui-widget-content {
  font-size: 12px;
  list-style-type: none;
  max-width: 400px;
}


